
export const SET_PLAYERNUM = 'SET_PLAYERNUM'
export const SET_GRIDNUM = 'SET_GRIDNUM'
export const INIT_DONE = 'INIT_DONE'
export const SET_PLAYERDATA = 'SET_PLAYERDATA'
export const SET_TURN = 'SET_TURN'



export function setPlayerTurn(data) {
    return { type: SET_TURN,payload:data }
}

export function setPlayerData(data) {
    return { type: SET_PLAYERDATA,payload:data }
}

export function initDone() {
    return { type: INIT_DONE }
}

export function setPlayerNum(num) {
    return { type: SET_PLAYERNUM, payload:num }
}

export function setGridNum(num) {
    return { type: SET_GRIDNUM, payload:num }
}
