import React, { Component } from 'react'
import { connect } from 'react-redux'
import { setPlayerNum, setGridNum, initDone, setPlayerData } from './action'
import { isNumber } from 'util';

class init extends Component {
    constructor(props) {
        super(props)
        this.state = {
            playerData: [{ id: 1, name: '' }, { id: 2, name: '' }],
            color: '',
            gridNum: 5
        }
    }
    checkForm() {
        const isNamePass = this.state.playerData.find(player => player.name === '') === undefined ? true : false
        const isColorPass = this.state.color !== '' ? true : false
        const isGridPass = Number(5 <= this.state.gridNum) && Number(this.state.gridNum <= 30) ? true : false
        if (isColorPass === true && isNamePass === true && isGridPass === true) {
            return true
        } else {
            alert('請確認顏色跟名字都有填上以及格子數在5~30之間')
            return false
        }
    }
    handleInit() {
        if (this.checkForm() === false) {
            return
        }
        let tempPlayerData
        const playerNum = this.state.playerData.length
        if (this.state.color === 'random') {
            const randomColor = require('randomcolor')
            const tempColorList = randomColor({
                count: playerNum,
                hue: 'random'
            });
            tempPlayerData = this.state.playerData.map(player => { return { ...player, color: tempColorList[player.id - 1] } })
        } else {
            tempPlayerData = this.state.playerData.map(player => {
                return ({
                    ...player,
                    color: '#000000'
                })
            })
        }
        this.props.dispatch(setGridNum(this.state.gridNum))
        this.props.dispatch(setPlayerNum(playerNum))
        this.props.dispatch(setPlayerData(tempPlayerData))
        this.props.dispatch(initDone())
    }
    handleChangePlayer(num) {
        let tempData = [...this.state.playerData]
        if (num === -1) {
            tempData.pop()
        } else {
            const dataLength = this.state.playerData.length
            tempData.push({ id: dataLength + 1, name: '' })
        }
        this.setState({ playerData: tempData })
    }
    handleChangePlayerName(name, id) {
        this.setState({
            playerData: [...this.state.playerData].map(player => {
                if (player.id === id) {
                    return { ...player, name: name }
                } else {
                    return player
                }
            })
        })
    }
    handleChangeColor(value) {
        this.setState({ color: value })
    }
    render() {
        const dataLen = this.state.playerData.length
        return (
            <div>
                請選擇邊格數
                <input type="number" min="5" max="30" defaultValue='5' onChange={(e) => this.setState({ gridNum: e.target.value })} />
                請選擇人數
                <button disabled={dataLen <= 2} onClick={() => this.handleChangePlayer(-1)}>-</button>
                {dataLen}
                <button disabled={dataLen >= 8} onClick={() => this.handleChangePlayer(1)}>+</button>
                {this.state.playerData.map(item =>
                    <div key={item.id}>
                        <span>玩家{item.id}</span>
                        <span>請輸入名稱<input type="text" onChange={(e) => this.handleChangePlayerName(e.target.value, item.id)} /></span>
                    </div>
                )}
                顏色分配
                隨機<input type="radio" name="color-radio" onChange={(e) => this.handleChangeColor(e.target.value)} value='random' />
                同色<input type="radio" name="color-radio" onChange={(e) => this.handleChangeColor(e.target.value)} value='allsame' />
                <button onClick={() => this.handleInit()} >開始遊戲</button>
            </div>
        )
    }
}
const mapDispatchToProps = (dispatch) => {
    return { dispatch }
}
export default connect(state => { return { state } }, mapDispatchToProps)(init)