import React, { Component } from 'react'
import { connect } from 'react-redux'
import { setPlayerTurn } from './action.js'
import './chessman.scss'
class chessman extends Component {
    constructor(props) {
        super(props)
        this.state = {
            allChess: [],
        }
        this.checkVectorList = [
            [1, 0], [1, 1], [0, 1], [-1, 1]
        ]
        this.playerNum = Number(this.props.state.playerNum)
        this.gridNum = Number(this.props.state.gridNum)
        this.gridW = this.props.layout.gridWidth
        this.sT = null
        this.eT = null
    }
    initChess() {
        let tempRow = []
        let tempChess = []
        for (let i = 0; i < this.gridNum; i++) {
            tempRow.push(0)
        }
        for (let i = 0; i < this.gridNum; i++) {
            tempChess.push([...tempRow])
        }
        this.setState({ allChess: [...tempChess] })
    }
    handleMoveChess(rowIndex, gridIndex) {
        this.sT = new Date().getTime();
        if (this.state.allChess[rowIndex][gridIndex] !== 0) {
            return
        }
        this.setChessState(rowIndex, gridIndex)
    }
    setChessState(rowIndex, gridIndex) {
        this.setState({
            allChess: this.state.allChess.map((row, _rowIndex) => {
                if (rowIndex === _rowIndex) {
                    return row.map((grid, _gridIndex) => {
                        if (gridIndex === _gridIndex) {
                            return this.props.state.nowPlayerTurn
                        } else {
                            return grid
                        }
                    })
                } else {
                    return row
                }
            })
        }, () => {
            this.checkIfWin(rowIndex, gridIndex)
        })
    }
    setChessState2(rowIndex, gridIndex) {
        let temp = [...this.state.allChess]
        temp[rowIndex][gridIndex] = this.props.state.nowPlayerTurn
        this.setState({ allChess: temp }, () => {
            this.checkIfWin(rowIndex, gridIndex)
        })
    }
    checkIfWin(rowIndex, gridIndex) {
        let isGameOver = false
        for (let vector of this.checkVectorList) {
            let accumulate = 0
            for (let linkNum = 1; linkNum < 6; linkNum++) {
                console.log(accumulate)
                let _vector = vector.map(item => item * linkNum)
                let checkPostion = [rowIndex + _vector[0], gridIndex + _vector[1]]
                if (checkPostion[0] > this.gridNum - 1 || checkPostion[1] > this.gridNum - 1 || checkPostion[0] < 0 || checkPostion[1] < 0) {
                    break
                }
                if (this.state.allChess[checkPostion[0]][checkPostion[1]] !== this.props.state.nowPlayerTurn) {
                    break
                }
                accumulate += 1
            }
            for (let linkNum = -1; linkNum > -6; linkNum--) {
                let _vector = vector.map(item => item * linkNum)
                let checkPostion = [rowIndex + _vector[0], gridIndex + _vector[1]]
                if (checkPostion[0] > this.gridNum - 1 || checkPostion[1] > this.gridNum - 1 || checkPostion[0] < 0 || checkPostion[1] < 0) {
                    break
                }
                if (this.state.allChess[checkPostion[0]][checkPostion[1]] !== this.props.state.nowPlayerTurn) {
                    break
                }
                accumulate += 1
            }
            if (accumulate === 4) { isGameOver = true }
        }
        if (isGameOver) {
            this.gameOver()
        }
        this.handlePlayerTurn()
    }
    gameOver() {
        alert(`${this.props.state.playerData.find(el => el.id === this.props.state.nowPlayerTurn).name}贏了`)
    }
    handlePlayerTurn() {
        if (this.props.state.nowPlayerTurn === this.playerNum) {
            this.props.dispatch(setPlayerTurn(1))
        } else {
            this.props.dispatch(setPlayerTurn(this.props.state.nowPlayerTurn + 1))
        }
        console.log(new Date().getTime() - this.sT)
    }
    handleChessBtnStyle(rowIndex, gridIndex) {
        const { intervalWidth } = this.props.layout
        return ({
            top: rowIndex * intervalWidth + intervalWidth / 2,
            left: gridIndex * intervalWidth + intervalWidth / 2,
            height: this.gridW,
            width: this.gridW
        })
    }
    handleSingleChessStyle(player) {
        if (player !== 0) {
            return ({ backgroundColor: this.props.state.playerData.find(playerInData => player === playerInData.id).color, border: '0.5px black solid' })
        }
    }
    componentDidMount() {
        this.initChess()
    }
    render() {
        const { gridWidth } = this.props.layout
        return (
            <section className='chessman-wrapper' >
                <section className='chess-game'>
                    {this.state.allChess !== [] ?
                        this.state.allChess.map((row, rowIndex) =>
                            <div key={rowIndex} className='chessman-row' style={{ height: gridWidth }}>
                                {row.map((grid, gridIndex) =>
                                    <span
                                        key={gridIndex}
                                        className='chess-btn'
                                        style={this.handleChessBtnStyle(rowIndex, gridIndex)}
                                        onClick={() => this.handleMoveChess(rowIndex, gridIndex)}
                                    >
                                        <div style={{
                                            height: this.gridW * 4 / 5,
                                            width: this.gridW * 4 / 5,
                                            borderRadius: this.gridW * 2 / 5,
                                            ...this.handleSingleChessStyle(grid)
                                        }}
                                            className="single-chess"
                                        ></div>
                                    </span>)
                                }
                            </div>)
                        :
                        null}</section>
            </section>
        )
    }
}

const mapDispatchToProps = (dispatch) => {
    return { dispatch }
}
export default connect(state => { return { state } }, mapDispatchToProps)(chessman)