const initialState = {
    playerNum: 2,
    gridNum: 15,
    isInited: false,
    playerData: [],
    nowPlayerTurn: 1
}
const reducer = (state = initialState, action) => {
    switch (action.type) {
        case 'INIT_DONE':
            return Object.assign({}, state, {
                isInited: true
            })
        case 'SET_PLAYERNUM':
            return Object.assign({}, state, {
                playerNum: action.payload
            })
        case 'SET_GRIDNUM':
            return Object.assign({}, state, {
                gridNum: action.payload
            })
        case 'SET_PLAYERDATA':
            return Object.assign({}, state, {
                playerData: action.payload
            })
        case 'SET_TURN':
            return Object.assign({}, state, {
                nowPlayerTurn: action.payload
            })
        default:
            return state
    }
}
export default reducer