import React from 'react'
import './board.scss'
function Checkerboard(props) {
  const gridNum = Number(props.gridNum)
  const {gridWidth,intervalWidth} = props.layout
  const totalWidth = (gridNum-1)*gridWidth + gridNum*intervalWidth
  const renderLayout = function (isRow) {
    let tempList = []
    for (let index = 0; index < gridNum ; index++) {
      if (isRow) {
        tempList.push(<div key={`row${index}`} className='single-row'></div>);
      } else {
        tempList.push(<span key={`column${index}`}  className='single-column'></span>);
      }
    }
    return tempList
  }
  return (
    <section className='layout-wrapper' style={{width:totalWidth,height:totalWidth, transform: `translate(${gridWidth/2}px,${gridWidth/2}px)`}}>
      <div className='layout-row'>
        {renderLayout(true)}
      </div>
      <div className='layout-column'>
        {renderLayout(false)}
      </div>
    </section>
  );
}
export default Checkerboard;